


















import Vue from 'vue';

export default Vue.extend({
  name: 'Carousel',
  props: {
    images: Array,
  },
  data() {
    return {
      currentSlide: 0,
    };
  },
  methods: {},
  computed: {
    carouselSize() {
      const screenWidth = document.documentElement.clientWidth;
      return screenWidth < 500 ? 300 : 400;
    },
    disposition(): number {
      return -this.currentSlide * this.carouselSize;
    },
  },
});
